<template>
  <v-card>
    <v-card-title>
      <v-btn @click="onBack" icon medium class="mr-1">
        <v-icon medium> mdi-arrow-left </v-icon>
      </v-btn>
      Writers
    </v-card-title>

    <v-card-text>
      <breadcrumbs :items="breadcrumbItems"></breadcrumbs>
      <v-row no-gutter>
        <v-col cols="12" lg="12" xl="12" md="12" sm="12" xs="12">
          <writers-grid v-if="params.uuid" :params="params" :getDataCb="getDataCb"> </writers-grid>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>
<script>
import WritersGrid from './writersGrid.vue';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';
import IconButton from '../../Common/IconButton.vue';
import Breadcrumbs from '../../Common/Breadcrumbs.vue';
import _get from 'lodash/get';
import routeConstant from '../../../constants/routes';
const baseHref = routeConstant.rfConsumers;

export default {
  name: 'writers',
  data() {
    return {
      params: {
        uuid: null,
        writers: true,
      },
      gridData: {},
      breadcrumbItems: [
        {
          text: 'Streams',
          disabled: false,
          link: baseHref,
        },
      ],
    };
  },
  components: {
    IconButton,
    WritersGrid,
    TooltipIconButton,
    Breadcrumbs,
  },
  mounted() {
    this.params = { ...this.params, uuid: this.$route.params.consumerId };
  },
  methods: {
    onBack() {
      this.$router.go(-1);
    },
    getDataCb(data) {
      this.gridData = data;
      this.updateBreadCrumbs();
    },
    updateBreadCrumbs() {
      if (this.breadcrumbItems.length > 1) {
        return;
      }
      if (!(this.gridData && this.gridData.extraData)) {
        return;
      }
      if (this.gridData.extraData.hasOwnProperty('stream_uuid')) {
        const streamUUID = _get(this.gridData, 'extraData.stream_uuid', '');
        this.breadcrumbItems.push({
          text: streamUUID,
          align: 'center',
          disabled: false,
          link: routeConstant.rfConsumers.replace(':streamId', streamUUID),
        });
      }

      if (this.gridData.extraData.hasOwnProperty('uuid')) {
        this.breadcrumbItems.push({
          text: 'Consumers',
          align: 'center',
          disabled: true,
        });

        const streamUUID = _get(this.gridData, 'extraData.stream_uuid', '');
        const consumerUUID = _get(this.gridData, 'extraData.uuid', '');

        this.breadcrumbItems.push({
          text: consumerUUID,
          align: 'center',
          disabled: true,
          link: routeConstant.rfWriters.replace(':streamId', streamUUID).replace(':consumerId', consumerUUID),
        });
      }

      // const streamName = _get(this.gridData.extraData, 'extraData.name', '');
      // if (streamName) {
      // this.breadcrumbItems.push({ text: streamName, align: 'center', disabled: true });
      // }
    },
  },
};
</script>
