function getEndpoint(uuid, endpoint) {
  if (uuid) {
    return endpoint + `/${uuid}`;
  }
  return endpoint;
}

export default {
  rfCommands: uuid => getEndpoint(uuid, '/rf-commands'),
  rfFlowNetwork: uuid => getEndpoint(uuid, '/rf-flow-network'),
  rfJobs: uuid => getEndpoint(uuid, '/rf-jobs'),
  rfNetworks: uuid => getEndpoint(uuid, '/rf-networks'),
  rfPoints: uuid => getEndpoint(uuid, '/rf-points'),
  rfProducers: uuid => getEndpoint(uuid, '/rf-producers'),
  rfConsumers: uuid => getEndpoint(uuid, '/rf-consumers'),
  rfWriters: uuid => getEndpoint(uuid, '/rf-writers'),
  rfStreams: uuid => getEndpoint(uuid, '/rf-streams'),
  rfDevices: uuid => getEndpoint(uuid, '/rf-devices'),
  rfWriterActions: uuid => getEndpoint(uuid, '/rf-writer-actions')
};
